import React, { Component } from 'react'
import PropTypes from 'prop-types';
import Mapbox from './Mapbox'
import JourneyContainer from '../containers/JourneyContainer'
import AppsContainer from '../containers/AppsContainer'
import ClimateContainer from '../containers/ClimateContainer'
import { selectUser, nextUser } from '../actions'
import '../styles/App.css';

const accessToken = 'pk.eyJ1Ijoic25ha2FqaW1hIiwiYSI6ImNqMGEwZ3BlNTBnYzUycXBkbnQyaGszd3kifQ.OspnfsEpf_U7vqCFQV9kIA';

class Window extends Component {
  constructor(props) {
    super();
    this.wasResized = this.wasResized.bind(this);
  }
  componentWillMount() {
    this.wasResized();
  }
  componentDidMount() {
    window.addEventListener('resize', this.wasResized);
    window.store.dispatch(selectUser(0));
  }
  componentWillUnmount() {
    window.removeEventListener('resize', this.wasResized);
  }
  static windowSize() {
    const documentElement = document.documentElement;
    const body = document.getElementsByTagName('body')[0];
    const width = window.innerWidth || documentElement.clientWidth || body.clientWidth;
    const height = window.innerHeight|| documentElement.clientHeight|| body.clientHeight;
    return {width, height};
  }
  wasResized() {
    const size = Window.windowSize();
    console.log(size);
    this.props.onResize(size);
  }
  render() {
    // mapbox://styles/mapbox/dark-v9
    const mapStyle = './mapbox_style_blue/style.json';
    const layout = this.props.layout;
    const user = this.props.user;
    console.log('user.image', user.image);
    if (typeof layout.size === 'undefined') {
      return <div />;
    }
    const stylePS = { width:layout.radius * 2, height:layout.radius * 2, borderRadius:layout.radius,
                      left:layout.size.width - layout.radius*2 - layout.margin };
    return (<div style={{fontSize:layout.fontSize}}>
            <Mapbox center={[-122.193, 47.707]} zoom={14}
                    mapStyle={mapStyle} accessToken={accessToken}>
            </Mapbox>
            <div style={{position:'absolute', backgroundSize:'100% 100%', width:layout.radius, height:layout.radius, borderRadius:layout.radius/2,
                              left:layout.size.width - layout.radius*3 - layout.margin*2, top:layout.margin + layout.radius/2,
                              backgroundColor:'black',
              backgroundImage:'url("./assets/ic_compass_needle@2x.png")'}}>
            </div>
            <div id='profileFrame' style={{...stylePS, top:layout.margin,
              backgroundImage:'url(' + user.image + ')'}}
              onClick={()=>{window.store.dispatch(nextUser())}}>
            </div>
            <div id='searchFrame' style={{...stylePS, top:layout.margin*2 + layout.radius*2,
              backgroundImage:'url("./assets/ic_search@2x.png")'}}>
            </div>
            <JourneyContainer />
            <div id='bottomFrame' style={{top:layout.bottomFrameTop, height:layout.bottomFrameHeight}}>
              <AppsContainer />
            </div>
            <div id='bottomBar' style={{top:layout.bottomBarTop, height:layout.bottomBarHeight}}>
              <ClimateContainer />
            </div>
            </div>);
  }
}

Window.propTypes = {
 onResize: PropTypes.func.isRequired,
 layout: PropTypes.object.isRequired,
 user: PropTypes.object.isRequired,
}

export default Window;



