import { connect } from 'react-redux'
import Window from '../components/Window'
import { setWindowSize } from '../actions'

const mapDispatchToProps = (dispatch) => {
  return {
    onResize: (size) => {
      dispatch(setWindowSize(size))
    }
  }
}

const mapStateToProps = (state) => {
  return {
    layout: state.layout,
    user:state.user
  }
}

const App = connect(
                             mapStateToProps,
                             mapDispatchToProps
                             )(Window)


export default App;



