import React from 'react';
import PropTypes from 'prop-types';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import ContactsContainer from '../containers/ContactsContainer.js';
import CalendarContainer from '../containers/CalendarContainer.js';
import TasksContainer from '../containers/TasksContainer.js';
import MediaContainer from '../containers/MediaContainer.js';
import '../styles/Apps.css';

const Apps = ({layout, app, onClick}) => {
  const paneWidth = layout.size.width * 3 / 4;
  const tabWidth = (paneWidth - layout.margin * 4) / 4;
  const tabStyle = {width:tabWidth, height:layout.fontSizeL, fontSize:layout.fontSizeL};
  const selected = app.selected;
  const paneStyle = {width:paneWidth, marginRight:layout.margin, height:layout.size.height/5, borderRadius:layout.margin/2};
  return (
    <div>
      <div style={{width:paneWidth, margin:'auto', height:layout.fontSize * 2}}>
        <div style={{ marginTop:layout.fontSize, paddingLeft:layout.margin * 2 }}>
          <div id='contacts' className='appTab' onClick={onClick} style={{...tabStyle, opacity:selected==='contacts' ? 1.0 : 0.5}}>Contacts</div>
          <div id='media' className='appTab' onClick={onClick} style={{...tabStyle, opacity:selected==='media' ? 1.0 : 0.5}}>Media</div>
          <div id='calendar' className='appTab' onClick={onClick} style={{...tabStyle, opacity:selected==='calendar' ? 1.0 : 0.5}}>Calendar</div>
          <div id='tasks' className='appTab' onClick={onClick} style={{...tabStyle, opacity:selected==='tasks' ? 1.0 : 0.5}}>Tasks</div>
          <div style={{clear:'both'}} />
        </div>
      </div>
      <div style={{position:'relative',
                   left:(layout.size.width - paneWidth) / 2 -(paneWidth + layout.margin) * 3,
                   width:paneWidth * 10, overflow:'hidden', marginTop:layout.margin}}>
        <CSSTransitionGroup
          transitionName='appPane'
          transitionEnterTimeout={300}
          transitionLeaveTimeout={300}>
          {app.extras.map((i) =>
            <div key={i} className='appPane' style={{marginRight:layout.margin, height:layout.size.height/4,
                         float:'left'}}>
            </div>
          )}
            <div key='contacts' className='appPaneActive' style={paneStyle}>
              <ContactsContainer />
            </div>
            <div key='media' id='mediaPane' className='appPaneActive' style={paneStyle}>
              <MediaContainer />
            </div>
            <div key='calendar' className='appPaneActive' style={paneStyle}>
              <CalendarContainer />
            </div>
            <div key='tasks' className='appPaneActive' style={paneStyle}>
              <TasksContainer  />
            </div>
          <div style={{clear:'both'}} />
        </CSSTransitionGroup>
      </div>
    </div>
  );
}

Apps.propTypes = {
  layout: PropTypes.object.isRequired,
  app: PropTypes.object.isRequired,
  onClick: PropTypes.func.isRequired,
}

export default Apps;



