import { connect } from 'react-redux'
import Journey from '../components/Journey'
import { showJourney, flyTo } from '../actions'

const mapDispatchToProps = (dispatch) => {
  return {
    openJourney: (layout) => {
      const selected = 0; // HACK: hard-coded for now
      dispatch(showJourney(true, layout, selected));
    },
    closeJourney: () => {
      dispatch(showJourney(false));
    },
    flyTo: (item, layout) => {
      dispatch(flyTo(item.loc, layout));
    },
  }
}

const mapStateToProps = (state) => {
  return {
    on: state.journey.on,
    route: state.journey.routes[state.journey.selected] || {},
    layout: state.layout,
  }
}

const JourneyFrame = connect(
                             mapStateToProps,
                             mapDispatchToProps
                             )(Journey)

export default JourneyFrame;



