import 'whatwg-fetch';
import mapboxgl from 'mapbox-gl/dist/mapbox-gl.js';
import { setCoordinates, flyTo } from '../actions'

var s_markers = [];

const journey = (state = {on:false, routes:[], selected:0}, action) => {
  switch (action.type) {
    case 'FLY_TO':
      const offset = action.layout.size.height / 20;
      const center = window.map.getCenter();
      const ptCenter = window.map.project(center);
      const centerOffset = window.map.unproject([ptCenter.x, ptCenter.y-offset]);
      window.map.easeTo({
        center: [action.loc[0], action.loc[1]-(centerOffset.lat - center.lat)]
      });
      return state; // unchanged

    case 'SHOW_JOURNEY':
      if (action.on) {
        const stops = state.routes[action.selected].stops;
        const locstr = stops.map((stop)=>(stop.loc[0] + ',' + stop.loc[1])).join(';');
        const url = 'https://api.mapbox.com/directions/v5/mapbox/driving/' + locstr;
        const query = '?steps=true&geometries=geojson&access_token=' + mapboxgl.accessToken;
        fetch(url + query,
            {credentials: 'same-origin'}
        ).then((res)=> {
          return res.json();
        }).then((json) => {
          const legs = json.routes[0].legs; // Always pick the first candidate
          const steps = legs.reduce((array, leg) => {
            return array.concat(leg.steps);
          }, []);
          const coords = steps.reduce((array, step) => {
            return array.concat(step.geometry.coordinates);
          }, [])
          window.store.dispatch(setCoordinates(coords));
          window.store.dispatch(flyTo(stops[0].loc, action.layout));
        });

        const iconWidth = window.layout.iconWidth;
        const iconHeight = iconWidth * 168 / 144; // HACK: Hard-coded aspect ratio
        stops.forEach((stop) => {
          var el = document.createElement('div');
          el.className = 'marker';
          el.style.backgroundImage = 'url(' + stop.image + ')';
          el.style.width = iconWidth + 'px';
          el.style.height = iconHeight + 'px';
          var marker = new mapboxgl.Marker(el, {offset:[-iconWidth/2, -iconHeight]});
          marker.setLngLat(stop.loc);
          marker.addTo(window.map);
          s_markers.push(marker);
        });
      } else {
        if (window.map.getLayer('route')) {
          window.map.removeLayer('route');
          window.map.removeSource('route');
        }
        s_markers.forEach((marker)=>{
          marker.remove();
        });
        s_markers = [];
      }
      return Object.assign({}, state, {on:action.on, selected:action.selected, coordinates:[]})

    case 'SET_ROUTES':
      return Object.assign({}, state, {routes:action.routes})

    case 'SET_COORDINATES':
      window.map.addLayer({
        id:'route',
        source:{
          type:'geojson',
          data:{
            type:'LineString',
            coordinates: action.coordinates
          }
        },
        type:'line',
        paint:{
          "line-width":8,
          "line-color":"rgba(0, 124, 191, 0.8)"
        },
        layout:{
          "line-join":'round',
          "line-cap":'round'
        }
      });
      return Object.assign({}, state, {coordinates:action.coordinates});

    default:
      return state
  }
}

export default journey



