import { setContacts, setTasks, setCalendar, setRoutes, showJourney, setMedia } from '../actions'

const s_users = [{
  image: "./assets/ic_user_01@2x.png",
  contacts: [
    { id:1, name:'Alden Harding', image:'./assets/ic_contact_01@2x.png' },
    { id:2, name:'Guo Tu', image:'./assets/ic_contact_02@2x.png' },
    { id:3, name:'Erika Johnson', image:'./assets/ic_contact_03@2x.png' },
  ],
  tasks: [
    { id:1, title:"Send demo for review", time:"11:00PM" },
    { id:2, title:"Call Brian", time:"8:30AM" },
  ],
  media: [
    { id:1, title:"Radioactive", album:"Night Visions", artist:"Image Dragons",
      image:"./assets/img_album_nightvisions@2x.png" },
  ],
  calendar: [
    { id:1, title:"Morning staff meeting", loc:"Xevo Kirkland office",
      from:'10:00AM', to:'11:00AM' },
    { id:2, title:"XAssis scrum meeting", loc:"Starwars",
      from:'11:00AM', to:'12:00PM' },
  ],
  routes: [{
    stops:[
      { id:0, title:"Current location", loc:[-122.1897, 47.7097], image:"./assets/ic_pin_currentlocation@2x.png" },
      { id:1, title:"Xevo Kirkland Office", eta:"9:47 AM", loc:[-122.1736, 47.7126], image:"./assets/ic_pin_office@2x.png" }
    ]
  }],
}, {
  image: "./assets/ic_user_02@2x.png",
  contacts: [
    { id:1, name:'Ratan Sandeep', image:'./assets/ic_contact_04@2x.png' },
    { id:2, name:'Hilary Thurstan', image:'./assets/ic_contact_05@2x.png' },
    { id:3, name:'Leroy Bryson', image:'./assets/ic_contact_06@2x.png' },
  ],
  tasks: [
    { id:1, title:"Buy a new book", time:"9:45am" },
    { id:2, title:"Send code revuew", time:"5:30PM" },
  ],
  media: [
    { id:1, title:"Super Dance", album:"Native", artist:"Xevo Symphony",
      image:"./assets/img_album_native@2x.png" },
  ],
  calendar: [
    { id:1, title:"Pick up kids", loc:"Kirkland School",
      from:'5:00PM', to:'5:10PM' },
    { id:2, title:"Dinner", loc:"Home",
      from:'6:30PM', to:'7:30PM' },
  ],
  routes: [{
    stops:[
      { id:0, title:"Current location", loc:[-122.1897, 47.7097], image:"./assets/ic_pin_currentlocation@2x.png" },
      { id:1, title:"Starbucks", eta:"9:43 AM", loc:[-122.1684, 47.7114], image:"./assets/ic_pin_starbucks@2x.png" },
      { id:2, title:"Kirkland High School", eta:"10:03 AM", loc:[-122.1671, 47.6990], image:"./assets/ic_pin_school@2x.png" }
    ]
  }],
}];

const setUser = (user) => {
  setTimeout(()=>{
    window.store.dispatch(setContacts(user.contacts));
    window.store.dispatch(setTasks(user.tasks));
    window.store.dispatch(setMedia(user.media));
    window.store.dispatch(setCalendar(user.calendar));
    window.store.dispatch(setRoutes(user.routes));
    window.store.dispatch(showJourney(false));
  }, 500);
}
const user = (state = {selected:0}, action) => {
  var user;
  switch (action.type) {
    case 'NEXT_USER':
      const selected = (state.selected + 1) % s_users.length;
      user = s_users[selected];
      setUser(user);
      return Object.assign({}, state, {selected:selected, image:user.image})

    case 'SELECT_USER':
      user = s_users[action.selected];
      setUser(user);
      return Object.assign({}, state, {selected:action.selected, image:user.image})

    default:
      return state
  }
}

export default user



