import React from 'react';
import PropTypes from 'prop-types';
import '../styles/Media.css';

const Media = ({layout, media}) => {
  const track = media[0]; // HACK: hard-coded index
  return (
    <div style={{padding:layout.fontSizeM, width:'100%', height:'100%',
    backgroundSize:'100%', backgroundPositionY:'center',
    backgroundImage:'url('+track.image+')'}}>
      <p style={{fontSize:layout.fontSizeM}}>{track.title}</p>
      <p>Night Visions</p>
      <p>Image Dragons</p>
    </div>
  );
}
Media.propTypes = {
  layout: PropTypes.object.isRequired,
  media: PropTypes.array.isRequired,
}

export default Media;



