const layout = (state = {}, action) => {
  switch (action.type) {
    case 'SET_WINDOW_SIZE':
      state = {
        size:action.size,
        margin:16 * action.size.height / 1600,
        radius:40 * action.size.height / 1600, // for search and profile
        btnRadius:36 * action.size.width / 1000, // for climate control
        bottomFrameHeight:action.size.height / 4,
        bottomBarHeight:action.size.height / 8,
        iconWidth:72 * action.size.height / 1600, // widht of route-icon
        fontSize: 22 * action.size.height / 1600,
        fontSizeB: 26 * action.size.height / 1600, // journey button size
        fontSizeM: 30 * action.size.height / 1600,
        fontSizeL: 36 * action.size.height / 1600,
      };
      Object.assign(state, {
        bottomFrameTop:state.size.height - state.bottomFrameHeight,
        bottomBarTop:state.size.height - state.bottomBarHeight,
        iconHeight:state.iconWidth * 168 / 144, // HACK: Hard-coded aspect ratio
      })
      window.layout = state; // HACK: Make is accessible from reducers
      return state;

    default:
      return state
  }
}

export default layout



