import React from 'react';
import PropTypes from 'prop-types';

const Climate = ({layout}) => {
  const diag = layout.btnRadius * 2;
  const btnStyle = { float:'left', width:diag, height:diag,
                     margin:layout.margin/2,
                     backgroundSize:'100% 100%' };
  return (
    <div style={{opacity:1.0, color:'white'}}>
      <div style={{position:'absolute', top:layout.fontSize * 2 + layout.margin/2, fontSize:layout.btnRadius * 2}}>
        <div style={{position:'absolute', left:layout.margin * 3}}>64</div>
        <div style={{position:'absolute', width:layout.size.width - layout.margin * 3, textAlign:'right'}}>65</div>
      </div>
      <div style={{width:(diag + layout.margin + 2) * 7, margin:'auto'}}>
        <div style={{height:layout.fontSize * 2}} />
        <div style={{textAlign:'center', marginTop:layout.margin}}>
          <div style={{...btnStyle, backgroundImage:'url("./assets/btn_climate_power_off_normal@2x.png")'}} ></div>
          <div style={{...btnStyle, backgroundImage:'url("./assets/btn_climate_ac_normal@2x.png")'}} ></div>
          <div style={{...btnStyle, backgroundImage:'url("./assets/btn_climate_fan2_normal@2x.png")'}} ></div>
          <div style={{...btnStyle, backgroundImage:'url("./assets/btn_climate_mode1_normal@2x.png")'}} ></div>
          <div style={{...btnStyle, backgroundImage:'url("./assets/btn_climate_airintake_normal@2x.png")'}} ></div>
          <div style={{...btnStyle, backgroundImage:'url("./assets/btn_climate_front_normal@2x.png")'}} ></div>
          <div style={{...btnStyle, backgroundImage:'url("./assets/btn_climate_rear_normal@2x.png")'}} ></div>
          <div style={{clear:'both'}} />
        </div>
      </div>
    </div>
);
}

Climate.propTypes = {
  layout: PropTypes.object.isRequired,
}

export default Climate;



