import React from 'react';
import PropTypes from 'prop-types';
import '../styles/Contacts.css';

class Contacts extends React.Component {
  render() {
    const {layout, contacts} = this.props;
    const contactHeight = layout.fontSizeM * 3 / 2;
    const userStyle = {width:contactHeight, height:contactHeight,
                       float:'left', backgroundSize:'100% 100%', marginRight:layout.margin,
                       borderRadius:contactHeight/2};
    const liStyle = {height:contactHeight, padding:layout.margin};
    return (
      <ul className='contacts'>
        {
          contacts.map((contact) => {
            return <li key={contact.id} className='contact' style={liStyle}>
              <div style={{...userStyle, backgroundImage:'url('+contact.image+')'}} />
              <p style={{ paddingTop:layout.fontSizeM-layout.fontSize }}>{contact.name}</p>
            </li>
          })
        }
      </ul>
    );
  }
}

Contacts.propTypes = {
  layout: PropTypes.object.isRequired,
  contacts: PropTypes.array.isRequired,
}

export default Contacts;



