import { connect } from 'react-redux'
import Tasks from '../components/Tasks'

const mapDispatchToProps = (dispatch) => {
  return {
    }
}

const mapStateToProps = (state) => {
  return {
    layout: state.layout,
    tasks: state.tasks
  }
}

const TasksContainer = connect(
                             mapStateToProps,
                             mapDispatchToProps
                           )(Tasks)

export default TasksContainer;



