import { connect } from 'react-redux'
import Apps from '../components/Apps'
import { selectApp } from '../actions'

const mapDispatchToProps = (dispatch) => {
  return {
    onClick: (e) => {
      dispatch(selectApp(e.target.id));
      //dispatch(setJourney(false));
    }
  }
}

const mapStateToProps = (state) => {
  return {
    layout: state.layout,
    app: state.app
  }
}

const AppsFrame = connect(
                             mapStateToProps,
                             mapDispatchToProps
                             )(Apps)

export default AppsFrame;



