import React from 'react';
import PropTypes from 'prop-types';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import '../styles/Journey.css';

const Journey = ({openJourney, closeJourney, on, route, layout, flyTo}) => {
  const paneWidth = layout.size.width*2/3;
  return (
    <div>
      {on ? '' : <div id='journeyButton' onClick={()=>{openJourney(layout)}}
        style={{position:'absolute',
                backgroundImage:'url("./assets/ic_pin_common@2x.png")',
                backgroundSize:layout.fontSizeB,
                backgroundPosition: '0.5em 0.3em',
                paddingLeft: layout.fontSizeB * 2,
                borderRadius:layout.fontSizeB * 0.9,
                fontSize:layout.fontSizeB,
                left:layout.margin, top:layout.bottomFrameTop - layout.fontSizeB * 2}}>
        {"Open Journey"}
      </div>}
      <div style={{position:'absolute', left:0, top:layout.size.height/2, height:layout.size.height/2}}>
      <CSSTransitionGroup
        transitionName='journey'
        transitionEnterTimeout={300}
        transitionLeaveTimeout={300}>
        {
          on ? <div style={{position:'absolute', height:layout.size.height/2, width:layout.size.width}} key='foo'>
            <div style={{width:paneWidth, height:layout.size.height/2, margin:'auto'}}>
              <div id='journeyTitle' style={{fontSize:layout.fontSizeL}}>Journey April 15</div>
              <div id='journeyButton' onClick={closeJourney} style={{borderRadius:layout.fontSizeB * 0.9, fontSize:layout.fontSizeB, float:'left', marginLeft:layout.margin}}>
                {"Close"}
              </div>
              <div style={{clear:'both'}} />
              <div className='journeyItem' style={{ borderRadius:layout.margin/2 }}>
                <div style={{margin:'auto', width:(layout.iconWidth + layout.margin) * route.stops.length}}>
                  <div className='route-icon' style={{paddingTop:layout.margin}}>
                    {
                      route.stops.map((item) => {
                        return <div key={item.id}
                                onClick={()=>{flyTo(item, layout)}}
                                style={{backgroundImage:'url(./assets/ic_pin_shadow@2x.png)',
                                        width:layout.iconWidth+layout.margin, height:layout.iconHeight+layout.margin,
                                        float:'left',
                                        backgroundSize:'100% 100%'}}>
                                <img src={item.image} style={{width:layout.iconWidth, height:layout.iconHeight,
                                    position:'relative', left:layout.margin/2, top:1}}/>
                              </div>
                      })
                    }
                  </div>
                  <div style={{clear:'both'}} />
                </div>
                <p className='route'>{route.stops.length-1} stop ETA {route.stops[route.stops.length-1].eta}</p>
                <ul className='route' style={{paddingTop:layout.margin}}>
                  {
                    route.stops.map((item) => {
                      return <li className='route' style={{paddingLeft:layout.margin}} key={item.id} onClick={()=>{flyTo(item, layout)}}>
                          <table>
                            <tbody>
                            <tr>
                              <th style={{ width:paneWidth * 0.79 - layout.margin }}>{item.title}</th>
                              <th style={{ width:paneWidth * 0.19 - layout.margin, textAlign:'right'}}>{item.eta || ''}</th>
                            </tr>
                            </tbody>
                          </table>
                        </li>
                    })
                  }
                </ul>
              </div>
            </div>
          </div> : []
        }
      </CSSTransitionGroup>
      </div>
    </div>
  )
}

Journey.propTypes = {
  flyTo: PropTypes.func.isRequired,
  openJourney: PropTypes.func.isRequired,
  closeJourney: PropTypes.func.isRequired,
  on: PropTypes.bool.isRequired,
  route: PropTypes.object.isRequired,
  layout: PropTypes.object.isRequired,
}

export default Journey;



