const app = (state = {selected:'media', extras:[0,1]}, action) => {
  switch (action.type) {
    case 'SELECT_APP':
      var extras=[];
      if (action.app === 'contacts') {
        extras = [0,1,2];
      } else if (action.app === 'media') {
        extras = [0,1];
      } else if (action.app === 'calendar') {
        extras = [0];
      }
      return Object.assign({}, state, {selected:action.app, extras:extras})

    default:
      return state
  }
}

export default app



