export const toggleFlag = () => {
  return {
    type: 'TOGGLE_FLAG'
  }
}

export const setWindowSize = (size) => {
  return {
    type: 'SET_WINDOW_SIZE', size:size
  }
}

export const showJourney = (on, layout, selected=0) => {
  return {
  type: 'SHOW_JOURNEY',
  on: on,
  layout:layout,
  selected: selected
  }
}

export const flyTo = (loc, layout) => {
  return {
  type: 'FLY_TO',
  loc: loc,
  layout: layout,
  }
}

export const selectApp = (app) => {
  return {
    type: 'SELECT_APP', app:app
  }
}

export const setContacts = (contacts) => {
  return {
    type: 'SET_CONTACTS', contacts:contacts
  }
}

export const loadCalendar = () => {
  return {
    type: 'LOAD_CALENDAR'
  }
}

export const setCalendar = (calendar) => {
  return {
    type: 'SET_CALENDAR', calendar:calendar
  }
}

export const setTasks = (tasks) => {
  return {
    type: 'SET_TASKS', tasks:tasks
  }
}

export const setMedia = (media) => {
  return {
    type: 'SET_MEDIA', media:media
  }
}

export const selectUser = (selected) => {
  return {
    type: 'SELECT_USER', selected:selected
  }
}

export const nextUser = () => {
  return {
    type: 'NEXT_USER'
  }
}

export const setRoutes = (routes) => {
  return {
    type: 'SET_ROUTES', routes:routes
  }
}

export const setCoordinates = (coordinates) => {
  return {
    type: 'SET_COORDINATES', coordinates:coordinates
  }
}



