import React, { Component } from 'react';
import PropTypes from 'prop-types';
import mapboxgl from 'mapbox-gl/dist/mapbox-gl.js';

//
// Note: We are not using the react package from Uber (https://github.com/uber/react-map-gl) for maximum flexibility.
//
class Mapbox extends Component {
  constructor(props) {
    super(props);
    mapboxgl.accessToken = props.accessToken;
  }

  componentDidMount() {
    this.map = new mapboxgl.Map({
                                container: this.refs.map,
                                style: this.props.mapStyle,
                                center: this.props.center,
                                zoom:this.props.zoom,
                                });
    window.map = this.map; // REVIEW: Is there better way to do this?

    // strictly for development
    this.map.on('click', (e) => { console.log('mapbox.click', e.lngLat)});
  }
  componentWillUnmount() {
    if (this.map) {
      this.map.remove();
    }
  }
  render() {
    return (<div>
              <div ref='map' className='map'></div>
              <div className='overlay'>{this.props.children}</div>
            </div>);
  }
}

Mapbox.propTypes = {
  zoom: PropTypes.number.isRequired,
  center: PropTypes.array.isRequired,
  mapStyle: PropTypes.string.isRequired,
  accessToken: PropTypes.string.isRequired
}

export default Mapbox;



